import { BackendModule, Resource } from "i18next";
import availableLanguages from "../../translations";

const translationLoader: BackendModule = {
  init: () => {},
  type: "backend",
  read: (
    language: string,
    namespace: string,
    callback: (error: Error | undefined | null, resource: Resource) => void
  ) => {
    let resource: Resource = {};
    let error: Error | undefined = undefined;

    try {
      resource = availableLanguages[language as keyof object];
    } catch (_error) {
      error = _error as Error;
    }

    callback(null, resource);
  },
};
export default translationLoader;
